@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap');

body {
  background: #f3f3f0;
}

textarea {
  height: 400px;
  width: 500px;
  border: 0;
  color: #3f3f3f;
  font-family: "SF Mono", "Monaco", "Inconsolata", "Fira Mono", "Droid Sans Mono", "Source Code Pro", monospace;
  padding: 15px;
  margin-top: 5px;
}

textarea.error {
  font-size: 10em;
  text-align: center;
  vertical-align: center;
}

h1 {
  font-family: 'DM Serif Display', serif;
  letter-spacing: 0.1em;
  color: #0c0b09;
  margin: 0;
  text-align: left;
}

.App {
  padding-top: 30px;
  margin: 0px auto;
  width: 100%;
  text-align: center;
}

#left-section,
#right-section {
  display: inline-block;
  vertical-align: top;
}

#left-section {
  margin-right: 30px;
}

button {
  border: 1px solid #0c0b09;
  padding: 15px;
  margin: 10px;
  background: #fff;
  cursor: pointer;
}

#info {
  position: absolute;
  bottom: 15px;
  left: 15px;
  color: #9b9b94;
}
